import React, { useContext,useState } from 'react'
import { NavLink } from 'react-router-dom'
import './Signup.css'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { LoginContext } from '../context/ContextProvider';

const Sign_in = () => {

    // const { account, setAccount } = useContext(LoginContext);

    const [logdata, setData] = useState({
        email: "",
        password: ""
    });
    console.log(logdata);

    const adddata = (e) => {
        const { name, value } = e.target;
        console.log(name, value);

        setData(() => {
            return {
                ...logdata,
                [name]: value
            }
        })
    };

    const senddata = async (e) => {
        e.preventDefault();

        const { email, password } = logdata;
        // console.log(email);
        try {
            const res = await fetch("/login", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    email, password
                })
            });

            const data = await res.json();
            console.log(data);

            if (res.status === 400 || !data) {
                console.log("invalid details");
                toast.error("Invalid Details !", {
                    position: "top-center"
                });
            } else {
                console.log("data valid");
                // setAccount(data);
                toast.success("Login Successfully done !", {
                    position: "top-center"
                });
                setData({ ...logdata, email: "", password: "" })
            }
        } catch (error) {
            console.log("login page ka error" + error.message);
        }
    };


    return (

        <>
            <section>
                <div className="sign_container">
                    <div className="sign_header">
                        {/* /logo */}
                    </div>
                    <div className="sign_form">
                        <form method='POST'>
                            <h1>Sign-In</h1>
                            <div className="form_data">
                                <label htmlFor='email'>College email id</label>
                                <input type='text' name='email' value={logdata.email} onChange = {adddata} id='email' />
                                {/* <input type='text' name='email' placeholder='Enter your college email' id='email' /> */}
                            </div>
                            <div className="form_data">
                                <label htmlFor='password'>Password</label>
                                {/* <input type='password' name='password' placeholder='atleast 8 characters' id='password' /> */}
                                <input type="password" onChange={adddata} value={logdata.password} name='password' placeholder='At least 6 char' id="password"/>
                            </div>
                            <button className='signin_btn' onClick={senddata}>Continue</button>
                        </form>

                    </div>
                    <div className="create_accountinfo">
                        <p>New To College Bucket</p>
                        <NavLink to="register"><button>Create your account</button></NavLink>
                    </div>
                </div>
                <ToastContainer/>
            </section>
        </>
    )
}

export default Sign_in


