import React, { useState } from 'react';

const ItemList = () => {
  const [items, setItems] = useState([5, 2, 8, 1, 10]);

  const handleSort = () => {
    const sortedItems = [...items].sort((a, b) => a - b);
    setItems(sortedItems);
  };

  return (
    <div>
      <h2>Item List</h2>
      <button onClick={handleSort}>Sort from Lowest to Highest</button>
      <ul>
        {items.map((item, index) => (
          <li key={index}>{item}</li>
        ))}
      </ul>
    </div>
  );
};

export default ItemList;
