import React from 'react'
import './Newnav.css'
import { NavLink } from 'react-router-dom'

export default function Newnav() {
  return (
    <div className='new_nav'>
        <div className="nav_data">
            <div className="left_data">
                <NavLink to="" className='buy'>Buy</NavLink>
                <NavLink to="/sell" className='sell'>Sell</NavLink>
                <NavLink to="/rent" className='rent'>Rent</NavLink>
                <NavLink to="/request" className='request'>Request</NavLink>
                <NavLink to="/donate" className='donate'>Donate Items</NavLink>
                <NavLink to="/service" className='service'>Customer services</NavLink>
            </div>
        </div>
      
    </div>
  )
}