import React, { useState, useEffect } from 'react';
import './SellPage.css'; // Import the CSS file for styling

const SellPage = () => {
  const [productName, setProductName] = useState('');
  const [productDetails, setProductDetails] = useState('');
  const [productPrice, setProductPrice] = useState('');
  const [productImage, setProductImage] = useState('');
  const [productQuantity, setProductQuantity] = useState('');
  const [sellItems, setSellItems] = useState([]);
  const [chatOpen, setChatOpen] = useState(false);
  const [chatMessages, setChatMessages] = useState([]);
  const [message, setMessage] = useState('');

  // Load persisted data from localStorage on page load
  useEffect(() => {
    const savedSellItems = localStorage.getItem('sellItems');
    const savedChatMessages = localStorage.getItem('chatMessages');

    if (savedSellItems) {
      setSellItems(JSON.parse(savedSellItems));
    }

    if (savedChatMessages) {
      setChatMessages(JSON.parse(savedChatMessages));
    }
  }, []);

  // Save data to localStorage whenever sellItems or chatMessages change
  useEffect(() => {
    localStorage.setItem('sellItems', JSON.stringify(sellItems));
  }, [sellItems]);

  useEffect(() => {
    localStorage.setItem('chatMessages', JSON.stringify(chatMessages));
  }, [chatMessages]);

  const handleProductSubmit = (e) => {
    e.preventDefault();

    const newProduct = {
      name: productName,
      details: productDetails,
      price: productPrice,
      image: productImage,
      quantity: productQuantity,
    };

    setSellItems([...sellItems, newProduct]);

    // Clear the form inputs
    setProductName('');
    setProductDetails('');
    setProductPrice('');
    setProductImage('');
    setProductQuantity('');
  };

  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.onload = (event) => {
      setProductImage(event.target.result);
    };

    reader.readAsDataURL(file);
  };

  const handleChatSubmit = (e) => {
    e.preventDefault();

    const newMessage = {
      user: 'User1', // Replace with the appropriate user information
      message: message,
    };

    setChatMessages([...chatMessages, newMessage]);

    // Clear the chat input
    setMessage('');
  };

  const toggleChat = () => {
    setChatOpen(!chatOpen);
  };

  return (
    <div className="sell-page">
      <h1>Sell Page</h1>

      <h2>Add Product</h2>
      <form onSubmit={handleProductSubmit}>
        <div className="form-group">
          <label htmlFor="productName">Product Name</label>
          <input
            type="text"
            id="productName"
            value={productName}
            onChange={(e) => setProductName(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="productDetails">Product Details</label>
          <input
            type="text"
            id="productDetails"
            value={productDetails}
            onChange={(e) => setProductDetails(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="productPrice">Product Price</label>
          <input
            type="number"
            id="productPrice"
            value={productPrice}
            onChange={(e) => setProductPrice(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="productImage">Product Image</label>
          <input
            type="file"
            id="productImage"
            onChange={handleImageUpload}
            required
          />
          {productImage && (
            <img src={productImage} alt="Product" className="preview-image" />
          )}
        </div>
        <div className="form-group">
          <label htmlFor="productQuantity">Product Quantity</label>
          <input
            type="number"
            id="productQuantity"
            value={productQuantity}
            onChange={(e) => setProductQuantity(e.target.value)}
            required
          />
        </div>
        <button type="submit">Add Product</button>
      </form>

      <h2>Sell Items</h2>
      <div className="sell-items">
        {sellItems.map((item, index) => (
          <div className="sell-item" key={index}>
            <h3>{item.name}</h3>
            <p>{item.details}</p>
            <img src={item.image} alt={item.name} />
            <p>Price: {item.price}</p>
            <p>Quantity: {item.quantity}</p>
            <button className="chat-button" onClick={toggleChat}>
              {chatOpen ? 'Close Chat' : 'Open Chat'}
            </button>
            {chatOpen && (
              <div className="chat-box">
                <h4>Chat</h4>
                {chatMessages.map((message, index) => (
                  <div className="chat-message" key={index}>
                    <strong>{message.user}: </strong>
                    <span>{message.message}</span>
                  </div>
                ))}
                <form className="chat-form" onSubmit={handleChatSubmit}>
                  <input
                    type="text"
                    placeholder="Message"
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                    required
                  />
                  <button type="submit">Send</button>
                </form>
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
};

export default SellPage;