import React, { useState, useEffect } from 'react';
import './Request.css'; // Import the CSS file for styling

const RequestItemsPage = () => {
  const [productName, setProductName] = useState('');
  const [productDescription, setProductDescription] = useState('');
  const [requestedItems, setRequestedItems] = useState([]);

  useEffect(() => {
    // Load requested items from local storage on page load
    const storedItems = localStorage.getItem('requestedItems');
    if (storedItems) {
      setRequestedItems(JSON.parse(storedItems));
    }
  }, []);

  useEffect(() => {
    // Update local storage when requested items change
    localStorage.setItem('requestedItems', JSON.stringify(requestedItems));
  }, [requestedItems]);

  const handleProductNameChange = (event) => {
    setProductName(event.target.value);
  };

  const handleProductDescriptionChange = (event) => {
    setProductDescription(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    if (productName.trim() === '' || productDescription.trim() === '') {
      return;
    }

    const newItem = {
      id: Date.now(),
      name: productName,
      description: productDescription,
      value: 0,
      chat: [] // Initialize an empty chat array for each item
    };

    setRequestedItems([...requestedItems, newItem]);
    setProductName('');
    setProductDescription('');
  };

  const handleIncreaseValue = (itemId) => {
    const updatedItems = requestedItems.map((item) => {
      if (item.id === itemId) {
        return { ...item, value: item.value + 1 };
      }
      return item;
    });

    setRequestedItems(updatedItems);
  };

  const handleSendMessage = (itemId, message) => {
    const updatedItems = requestedItems.map((item) => {
      if (item.id === itemId) {
        return { ...item, chat: [...item.chat, message] };
      }
      return item;
    });

    setRequestedItems(updatedItems);
  };

  return (
    <div className="request-items-page">
      <h1>Request Items Page</h1>
      <form className="request-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="product-name">Product Name:</label>
          <input
            type="text"
            id="product-name"
            className="form-control"
            value={productName}
            onChange={handleProductNameChange}
          />
        </div>
        <div className="form-group">
          <label htmlFor="product-description">Product Description:</label>
          <textarea
            id="product-description"
            className="form-control"
            value={productDescription}
            onChange={handleProductDescriptionChange}
          />
        </div>
        <button type="submit" className="btn btn-primary">Submit</button>
      </form>

      <h2>Requested Items</h2>
      <ul className="requested-items-list">
        {requestedItems.map((item) => (
          <li key={item.id}>
            <div className="item-details">
              <div>
                <strong>Name:</strong> {item.name}
              </div>
              <div>
                <strong>Description:</strong> {item.description}
              </div>
              <div>
                <strong>Value:</strong> {item.value}
              </div>
            </div>
            <div className="chat-box">
              <h3>Chat</h3>
              <ul>
                {item.chat.map((message, index) => (
                  <li key={index}>{message}</li>
                ))}
              </ul>
              <div className="chat-input">
                <input
                  type="text"
                  placeholder="Type your message"
                  onKeyDown={(event) => {
                    if (event.key === 'Enter') {
                      handleSendMessage(item.id, event.target.value);
                      event.target.value = '';
                    }
                  }}
                />
              </div>
            </div>
            <button onClick={() => handleIncreaseValue(item.id)} className="btn btn-secondary">
              Increase Value
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default RequestItemsPage;
