import React, { useState, useEffect } from 'react';
import './Donate.css';

const DonateItemsPage = () => {
  const [productName, setProductName] = useState('');
  const [productDescription, setProductDescription] = useState('');
  const [productImage, setProductImage] = useState('');
  const [donateItems, setDonateItems] = useState([]);
  const [chatMessages, setChatMessages] = useState({});

  useEffect(() => {
    // Retrieve the stored donate items from local storage on page load
    const storedItems = localStorage.getItem('donateItems');
    if (storedItems) {
      setDonateItems(JSON.parse(storedItems));
    }
  }, []);

  const handleFormSubmit = (e) => {
    e.preventDefault();

    // Create a new donate item object
    const newItem = {
      id: Date.now(),
      productName,
      productDescription,
      productImage,
    };

    // Update the donate items list
    const updatedItems = [...donateItems, newItem];
    setDonateItems(updatedItems);

    // Store the updated donate items in local storage
    localStorage.setItem('donateItems', JSON.stringify(updatedItems));

    // Clear the form inputs
    setProductName('');
    setProductDescription('');
    setProductImage('');
  };

  const handleDeleteItem = (id) => {
    // Filter out the deleted donate item
    const updatedItems = donateItems.filter((item) => item.id !== id);
    setDonateItems(updatedItems);

    // Store the updated donate items in local storage
    localStorage.setItem('donateItems', JSON.stringify(updatedItems));
  };

  const handleChatMessage = (id, message) => {
    // Add the chat message to the respective donate item
    const updatedMessages = { ...chatMessages };
    if (updatedMessages[id]) {
      updatedMessages[id].push(message);
    } else {
      updatedMessages[id] = [message];
    }
    setChatMessages(updatedMessages);
  };

  return (
    <div className="donate-items-container">
      <h1 className="title">Donate Items Page</h1>

      <form onSubmit={handleFormSubmit} className="form">
        <h2>Add a new item</h2>
        <label>
          Product Name:
          <input
            type="text"
            value={productName}
            onChange={(e) => setProductName(e.target.value)}
          />
        </label>
        <br />
        <label>
          Product Description:
          <textarea
            value={productDescription}
            onChange={(e) => setProductDescription(e.target.value)}
          />
        </label>
        <br />
        <label>
          Product Image URL:
          <input
            type="text"
            value={productImage}
            onChange={(e) => setProductImage(e.target.value)}
          />
        </label>
        <br />
        <button type="submit">Add Item</button>
      </form>

      <h2 className="list-title">Donate Items List</h2>
      {donateItems.length === 0 ? (
        <p>No items to display.</p>
      ) : (
        <ul className="item-list">
          {donateItems.map((item) => (
            <li key={item.id} className="item">
              <h3>{item.productName}</h3>
              <p>{item.productDescription}</p>
              <img src={item.productImage} alt={item.productName} />
              <button
                onClick={() => handleDeleteItem(item.id)}
                className="delete-btn"
              >
                Delete Item
              </button>

              <h4 className="chat-title">Chat Box</h4>
              {chatMessages[item.id] &&
                chatMessages[item.id].map((message, index) => (
                  <p key={index} className="chat-message">{message}</p>
                ))}
              <input
                type="text"
                placeholder="Type a message..."
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    handleChatMessage(item.id, e.target.value);
                    e.target.value = '';
                  }
                }}
                className="chat-input"
              />
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default DonateItemsPage;
