import React, { useState, useEffect } from 'react';
import './Rent.css'; // Import your custom styles

const RentItemsPage = () => {
  const [productName, setProductName] = useState('');
  const [productDescription, setProductDescription] = useState('');
  const [costPerHour, setCostPerHour] = useState('');
  const [rentItems, setRentItems] = useState([]);
  const [chatMessage, setChatMessage] = useState('');

  useEffect(() => {
    const storedRentItems = localStorage.getItem('rentItems');
    if (storedRentItems) {
      setRentItems(JSON.parse(storedRentItems));
    }
  }, []);

  useEffect(() => {
    localStorage.setItem('rentItems', JSON.stringify(rentItems));
  }, [rentItems]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const newRentItem = {
      productName,
      productDescription,
      costPerHour,
      rented: false,
    };
    setRentItems([...rentItems, newRentItem]);
    setProductName('');
    setProductDescription('');
    setCostPerHour('');
  };

  const handleRent = (index) => {
    const updatedRentItems = rentItems.map((item, i) => {
      if (i === index) {
        return { ...item, rented: true };
      }
      return item;
    });
    setRentItems(updatedRentItems);
  };

  const handleUnrent = (index) => {
    const updatedRentItems = rentItems.map((item, i) => {
      if (i === index) {
        return { ...item, rented: false };
      }
      return item;
    });
    setRentItems(updatedRentItems);
  };

  const handleChatMessageChange = (e) => {
    setChatMessage(e.target.value);
  };

  const handleChatSend = (index) => {
    // Handle chat send logic here
    console.log('Sending chat message:', chatMessage);
    setChatMessage('');
  };

  return (
    <div className="rent-items-page">
      <h1>Rent Items</h1>

      <form onSubmit={handleSubmit}>
        <input
          type="text"
          placeholder="Product Name"
          value={productName}
          onChange={(e) => setProductName(e.target.value)}
          required
        />
        <textarea
          placeholder="Product Description"
          value={productDescription}
          onChange={(e) => setProductDescription(e.target.value)}
          required
        />
        <input
          type="number"
          placeholder="Cost per Hour"
          value={costPerHour}
          onChange={(e) => setCostPerHour(e.target.value)}
          required
        />
        <button type="submit">Add Item</button>
      </form>

      <div className="rent-items-list">
        <h2>Renting Items</h2>
        {rentItems.map((item, index) => (
          <div className="rent-item" key={index}>
            <div className="item-details">
              <h3>{item.productName}</h3>
              <p>{item.productDescription}</p>
              <p>Cost per Hour: {item.costPerHour}</p>
            </div>
            {!item.rented ? (
              <button onClick={() => handleRent(index)}>Rent</button>
            ) : (
              <div className="rented-label">
                <button onClick={() => handleUnrent(index)}>Unrent</button>
                <div>Rented</div>
              </div>
            )}
            <div className="chat-box">
              <input
                type="text"
                placeholder="Type your message"
                value={chatMessage}
                onChange={handleChatMessageChange}
              />
              <button onClick={() => handleChatSend(index)}>Send</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default RentItemsPage;
